import { Card } from "antd";
import { useEffect, useMemo, useState } from "react";
import {
  AlertDataProps,
  ErrorDataType,
  ErrorEnum,
  SyntxErrorProps,
} from "../AlertSDK/core/types";
import { getSourceCode } from "./utils";

export interface AlertItemProps {
  data: AlertDataProps;
}
const AlertItem: React.FC<AlertItemProps> = ({ data }) => {
  const errorData = useMemo<ErrorDataType>(() => JSON.parse(data.data), [data]);
  const [code, setCode] = useState("");
  useEffect(() => {
    if (errorData.type !== ErrorEnum.PROMISE) genCode();
  }, []);

  const genCode = async () => {
    const res = await getSourceCode(data, errorData as SyntxErrorProps);
    // @ts-ignore
    setCode(res);
  };

  const handleClick = () => {};

  return (
    <Card
      title={errorData.message}
      extra={
        <a href="#" onClick={handleClick}>
          Fixed
        </a>
      }
      style={{ width: 500 }}
    >
      <p>
        $
        {errorData.type === ErrorEnum.PROMISE ? "Promise " : "JS "}
      </p>
      <p></p>
      {errorData.type === ErrorEnum.PROMISE ? (
        <div> {errorData.message}</div>
      ) : (
        <code
          style={{ textAlign: "left" }}
          dangerouslySetInnerHTML={{ __html: code }}
        >
          {}
        </code>
      )}
    </Card>
  );
};

export default AlertItem;
